// An highlighted block
<template>
	<view class="content">
		<view class="avatorWrapper">
			<image class="img" mode="widthFix" src="/static/img/hzau.png"></image>
		</view>

		<view class="form">
			<view class="inputWrapper">
				<view class="input">
					<input class="baseInput loginInput" type="text" v-model="account" placeholder="请输入手机号/邮箱" />
				</view>
			</view>
			<view class="inputWrapper">
				<view class="input">
					<input class="baseInput loginInput" type="password" v-model="password" placeholder="请输入密码" />
				</view>
			</view>

			<view class="btn" @click="signIn">
				<text>登录</text>
			</view>

			<view class="loginTool">
				<view @click="toRegist()">注册账号</view>
				<view @click="toLost()">找回密码</view>
			</view>

		</view>

	</view>
</template>

<script>
	import {
		login
	} from '@/api/login'
	import {
		testLoginInfo
	} from '@/api/login/loginInfo'

	export default {
		data() {
			return {
				account: '',
				password: '',
				showPassword: true
			}
		},
		onShow() {
			this.account = uni.getStorageSync('account')
			this.password = uni.getStorageSync('password')
		},

		methods: {
			signIn() {
				// 检查输入
				if (!testLoginInfo(this.account, this.password)) return
				login({
					account: this.account,
					password: this.password,
					success(data) {
						// 登录成功，保存token和用户账号状态
						uni.setStorageSync('token', data.token)
						uni.setStorageSync('userState', true)
						// 保存登录信息到本地（账号、密码），方便下次登录
						uni.setStorageSync('account', this.account)
						uni.setStorageSync('password', this.password)
						setTimeout(() => {
							uni.switchTab({
								url: '/pages/tabbar/angle/angle',
							})
						}, 500)
					}
				})
			},
			toRegist() {
				uni.navigateTo({
					url: '/pages/login/register',
				})
			},
			toLost() {
				uni.navigateTo({
					url: '/pages/login/lost'
				})
			}
		}
	}
</script>

<style lang="less">
	@import '../../static/css/base.less';

	.form {
		box-sizing: border-box;
		padding: 0 10rpx;
	}

	.loginInput {
		height: 85rpx !important;
		border-radius: 38rpx !important;
		background-color: red;
	}

	.loginTool {
		display: flex;
		justify-content: space-around;
		margin-top: 50rpx;
		color: #888;

		view {
			padding: 10rpx 20rpx;
		}
	}
</style>
